<template>
	<div>
		<p>注册界面</p>
		<p>账号：<input type="text" v-model="zhang"/></p>
		<p>密码：<input type="text" v-model="mi"/></p>
		<button @click="zhu">注册</button>
	</div>
</template>

<script>
	export default {
		data(){
			return{
			  zhang:'',
			  mi:''
			}
		},
		methods:{
			zhu(){
				if(this.zhang && this.mi){
					 this.$store.state.people[this.zhang]=this.zhang;
					 this.$store.state.people[this.mi]=this.mi;
					 alert('注册成功!');
					 this.$router.push('/login');
				}else{
					alert('请完整输入注册的账号以及密码！');
				}
			}
		}
	}
</script>

<style>
</style>


