<template>
	<div>
		<p>您已经成功登录!</p>
		<p>{{this.seconds}}s后会为您跳转到支付界面</p>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				seconds:4
			}
		},
		mounted(){
			this.change();
		},
		methods:{
			change(){
				var timer=null;
				var _this=this;
				console.log(this.seconds)
				timer=setInterval(function(){
					_this.seconds--;
					if(_this.seconds==-1){
						_this.$router.push('/pay');
						clearInterval(timer);
					}
				},1000)
			}
		}
	}
</script>

<style>
</style>


