<template>
	<div>
		<p>登录界面</p>
		<p>账号：<input type="text" v-model="zhang"/></p>
		<p>密码：<input type="password" v-model="mi"/></p>
		<button @click="login" >登录</button>  <button><router-link :to='{name:"zhuce"}'>注册</router-link></button>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				zhang:'',
				mi:''
			}
		},
		methods:{
			login(){
				if(this.$store.state.people[this.zhang]==this.zhang&&this.$store.state.people[this.mi]==this.mi){
					this.$store.state.logined=true;
					alert('登录成功！');		
					this.$router.push('/logined');
					this.$store.state.zhang=this.zhang;
					this.zhang='';
					this.mi='';
				}else{
					alert('账号密码错误或是未注册！');
					this.$store.state.logined=false;
					this.zhang='';
					this.mi='';
				}
			}
		}
	}
</script>

<style>
</style>


