import login from '../components/login'
import logined from '../components/logined'
import zhuce from '../components/zhuce'
import pay from '../components/pay'

export default  [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/zhuce',
      name: 'zhuce',
      component: zhuce
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay
    }, {
      path: '/logined',
      name: 'logined',
      component: logined
    }
  ]




